import React from 'react'

import main_foto from '../interface/img/developer.jpg'

const AboutMe = () => {

    const viewAbout = () => {
        const block = document.body.querySelector('.about_block')

        const coords = block.getBoundingClientRect()

        if(coords.top < window.innerHeight - 100){
            block.classList.add('opacityShow')
        }
    }

    const openText = () => {
        const blockIn = document.body.querySelector('.text_info_about')
        blockIn.classList.toggle('text_info_about--active')

        const blockText = document.body.querySelector('.selftext_info_about')
        blockText.classList.toggle('none')
    }

    window.addEventListener('scroll', viewAbout)

    return (
        <React.Fragment>  
            <section className='about_section'>
                <div className='container'>
                    <div className='about_block'>
                        
                        <div className='title-medium' >
                        Обо мне
                        </div>

                        <div className='about'>
                            <div className='info_about'>
                                <h1>Я - Немыкин Артём.</h1>
                                <h1>Я - фронтенд-разработчик.</h1>
                                <p>Я знаком и работал со всеми технологиями, про которые где-нибудь упомянул. Сейчас нахожусь на стадии нахождения постоянной дисциплины в виде разработки настоящих коммерческих проектов для настоящих людей. Охотно буду изучать все технологии, которые доселе мне не попадались</p>
                                <div className='subtitle-premedium'>Ниже я написал больше информации о себе, перестарался, и оно не вписывалось в рамки этого блока. Если интересно, то вот:</div>
                                <div className='text_info_about' onClick={() => openText()}>
                                    <h3>Тыкни сюда, если заинтересовало</h3>
                                    
                                    <div className='selftext_info_about none'>
                                        <p>Своё знакомство с программированием я начал несколько лет назад. Начинал, как и многие в наше время, с python из-за его пестрящей легкости (что в дальнейшем изучении, как по мне, играет не на пользу). Создавал десктопные приложения, тогда мне понравилось гармонично расставлять элементы в интерфейсе. Спустя время, в голову пришла идея заняться создаем своего сайта. Все свои знания в виде одного питона я сложил в кулек и пошёл (пальцем в небо) в бэкенд. Если в двух словах, изучил Django Framework и даже написал целый сайт.</p>
                                        <p>Эти технологии не связаны с моей нынешней деятельностью, поэтому окончу их описывать словами о том, что мне многое не понравилось. Тогда я засмотрелся на JavaScript - язык, идеально воплотивший в себя функциональность и красоту. JS - это не пайтон. Да, точку с запятой тебя тоже ставить не заставляют, но этот язык грамотнее смотрится со стороны. В общем, я подступался к нему медленно - с html и css, часть которых уже знал и имел опыт работы. Потратив какое-то время на теорию, приступил к практике - задачки на литкоде и тп. </p>
                                        <p>Время шло, я верстал для практики, смотрел ролики с макетами, и когда понял, что из них я выжил всё, что мог - начал смотреть в сторону React. Первая такая сильная работа - интернет-магазин одежды. Я вложил в этот проект столько, сколько успел за короткий прометужток времени. Мне понравилось, насколько мобильно и гибко можно разрабатывать продукт.</p>
                                        <p>А теперь я тут. Это моё портфолио, и мои небольшие проекты, которые я делал для себя, но теперь могу показать другим, так как не собираюсь останавливаться на этом. Мне есть, что ещё сказать, но моё вступление затянулось, и придется немного изменить этот блок, ибо столько расписывать я не планировал.</p>
                                    </div>
                                   
                                </div>

                            </div>
                            <div className='any_about'>
                                <img src={main_foto} className='foto_about'/>
                                {/* <img src={nice_foto} className='nice_foto_about'/> */}
                            </div>
                        </div>

                    </div>
                </div>
            </section>
        </React.Fragment>
    )
}

export default AboutMe;


