import { makeAutoObservable } from "mobx";

class NotebookPhone {

    constructor(){
        this._notes = [
            {
                'number': '1',
                'title': 'Моя первая заметка!',
                'text': 'Вот тут я её и писал...'
            },
            {
                'number': '2',
                'title': 'Тут что-то важное',
                'text': 'Наверно'
            },
        ]
        makeAutoObservable(this)
    }

    setNotes(notes){
        this._notes = notes
    }
    
    get notes(){
        return this._notes
    }

}

export default NotebookPhone;